/* STATIC STYLES (desktop-first) */

.shine-card3 {
  position: relative;

  padding: 2rem;

  overflow: hidden;
  border-radius: 2rem;

  background: var(--color-primary);

  transition: box-shadow 0.4s ease;
}

.shine-card3-inner {
  position: relative;
  z-index: 2;
}

.shine-card3::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  z-index: 1;

  width: 200%;
  height: 200%;

  background: radial-gradient(
  circle at var(--shine-x, 50%) var(--shine-y, 50%),
  rgba(255, 255, 255, var(--shine-opacity, 0.5)),
  transparent 60%
  );

  transition: background 0.2s ease;
  pointer-events: none;
}

.shine-card3.glow:hover {
  box-shadow: 0 0 30px var(--glow-color, rgba(255, 255, 255, 0.3));
}

