/* STATIC STYLES (desktop-first) */

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;

  gap: 5rem;
}

.contact-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  width: min(50rem, 90vw);

  gap: 5rem;
}

.contact-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  gap: 1rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  .contact-cards {
    gap: 4rem;
  }
}

@media (max-width: 992px) {
  .contact-cards {
    gap: 3rem;
  }
}

@media (max-width: 768px) {
}

@media (max-width: 576px) {
  .contact-cards {
    grid-template-columns: repeat(1, 1fr);

    width: 60vw;

    gap: 2rem;
  }
}
